export default {
    name: 'store-pick',
    data() {
        return {
            mchIndex: null,
            list: null,
            firstGoodsId: null,
            plugin: null,
            manualLocation: false,
            longitude: '',
            latitude: '',
            locationAddress: null,
            keyword: '',
        };
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.mchIndex = options.mchIndex;
        this.firstGoodsId = options.firstGoodsId || null;
        this.plugin = options.plugin || null;
        
    },
    onShow() {
        if (!this.manualLocation) this.getLocation();
    },
    methods: {
        //搜索
        clear() {
            uni.hideKeyboard()
            this.keyword = '';
            this.loadData();
        },
        search() {
            this.loadData();
        },

        getLocation() {
            uni.showLoading({
                mask: true,
                title: '正在获取位置信息',
            });

            uni.getLocation({
                success: (e) => {
                    uni.hideLoading();
                    this.longitude = e.longitude;
                    this.latitude = e.latitude;
                    this.loadData();
                },
                fail: () => {
                    uni.hideLoading();
                    uni.showModal({
                        title: '提示',
                        content: '获取位置信息失败，需要授权获取您的位置信息',
                        showCancel: false,
                        confirmText: '打开授权',
                        success(e) {
                            if (e.confirm) {
                                uni.openSetting({});
                            }
                        }
                    });
                },
            });



















        },
        selectLocation() {
            this.manualLocation = true;
            uni.chooseLocation({
                success: e => {
                    this.longitude = e.longitude;
                    this.latitude = e.latitude;
                    this.locationAddress = (e.address || '') + ' ' + e.name || '';
                    this.locationAddress = this.locationAddress.trim();
                    this.loadData();
                }
            });
        },
        loadData() {
            uni.showLoading({
                mask: true,
                title: '加载中',
            });
            this.$request({
                url: this.plugin === 'booking' ? this.$api.book.store_list : this.$api.order.store_list,
                data: {
                    keyword: this.keyword,
                    longitude: this.longitude,
                    latitude: this.latitude,

                    goods_id: this.firstGoodsId,
                }
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    this.list = response.data.list;
                } else {
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
        setData(data) {
            if (this.plugin === 'gift') {
                let store_id = this.$store.state.gift.store_id;
                store_id = data;
                this.$store.commit('gift/storeId', data);
            } else {
                const formData = this.$store.state.orderSubmit.formData;
                formData.list[this.mchIndex].store_id = data;
                this.$store.commit('orderSubmit/mutSetFormData', formData);
            }
            uni.navigateBack();
        },
    }
}
